.output-container {
  position: relative;
  top: 200px;
  z-index: -1;
  width: 320px;
  border-radius: 10px;
  text-align: center;
  padding: 30px;
  margin-bottom: 20px;
  border-top: 2px solid #b42727;
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(56, 56, 56, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(top, rgba(143, 140, 140, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
  background: linear-gradient(to bottom, rgba(143, 140, 140, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000', GradientType=0);
  /* IE6-9 */
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  overflow: hidden; }
  .output-container.hide {
    top: 200px; }
  .output-container.show {
    top: 0; }
  @media (max-width: 600px) {
    .output-container {
      background: -webkit-linear-gradient(top, rgba(104, 103, 103, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
      background: linear-gradient(to bottom, rgba(104, 103, 103, 0.65) 0%, rgba(0, 0, 0, 0) 100%); } }
  .output-container span.output-string {
    display: inline-block;
    font-size: 50px;
    position: relative;
    width: 100%;
    height: 40px;
    color: #d6e0aa;
    padding-top: 15px; }
  .output-container span.equations {
    letter-spacing: 3px;
    position: relative;
    font-size: 16px;
    color: azure;
    display: inline-block;
    width: 100%;
    height: 20px;
    text-align: right; }
    .output-container span.equations:after {
      content: '';
      position: absolute;
      height: 2px;
      width: 10px;
      -webkit-animation-name: output-animation;
              animation-name: output-animation;
      -webkit-animation-duration: 1s;
              animation-duration: 1s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
      bottom: 0px; }
    .output-container span.equations:before {
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      background-color: #7c7c7c;
      bottom: -10px; }
      @media (max-width: 600px) {
        .output-container span.equations:before {
          background-color: #494848; } }

@-webkit-keyframes output-animation {
  0% {
    background-color: #fff8be; }
  50% {
    background-color: rgba(0, 0, 0, 0); }
  100% {
    background-color: #fff8be; } }

@keyframes output-animation {
  0% {
    background-color: #fff8be; }
  50% {
